@charset "UTF-8";
@import-normalize;
html {
  box-sizing: border-box;
  font-size: 62.5%;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body,
blockquote, figure,
form,
h1, h2, h3, h4, h5, h6,
ol, ul, p, pre {
  margin: 0;
}

body {
  font-size: 1.6rem;
}

img {
  max-width: 100%;
}

ul {
  padding-left: 0;
}

li {
  list-style: none;
}

button {
  border: 0;
  background: unset;
  color: currentColor;
  cursor: pointer;
}

.l-testimonials, .l-testimonials section, .l-plugins, .l-templates, .l-templates__cards article, .l-featured, .l-featured__features, .l-featured__features li, .l-footer, .l-footer-nav, .l-header, .flex {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
}

p.big {
  font-size: var(--fsz__big);
}

.l-testimonials article, .l-plugins article {
  flex: 1;
  min-width: 28rem;
  box-shadow: 0px 0px 5px 2px var(--violet-1);
  padding: 1em;
  margin-bottom: 1em;
  text-align: center;
}
.l-testimonials article h3, .l-plugins article h3 {
  padding: 1em;
  text-align: center;
}

:root {
  --color__main: #2f0743;
  --violet-1: #41295a;
  --violet-2: #513177;
  --violet-3: #a561cc;
  --color__link--alt: #b298dc;
  --cyan-0: #E1D3E9;
  --green-0: #1a4301;
  --green-1: #173801;
  --green-2: #376812;
  --green-3: #3E7114;
  --green-4: #CBEEA1;
  --grad__main: linear-gradient( $color__main, var(--violet-1), $color__main );
  --fsz__big: 1.2em;
  --nav-offset: 3em;
}
@media (min-width: 768px) {
  :root {
    --fsz__big: 1.6em;
  }
}

html {
  scroll-behavior: smooth;
  scroll-padding-top: var(--nav-offset);
}

body {
  color: var(--violet-1);
  background-color: #d9e4f5;
  background-image: linear-gradient(315deg, #d9e4f5 0%, #f5e3e6 74%);
  font-family: "Montserrat", sans-serif;
  height: 100%;
}

h1, h2, h3 {
  color: var(--violet-1);
  font-family: "Nunito", sans-serif;
  text-transform: uppercase;
}

h2 {
  font-size: 1.6em;
}

h3 {
  font-size: 1.4em;
}

h4 {
  font-size: 1.2em;
}

article {
  padding-top: var(--nav-offset);
}

main {
  width: 90%;
  max-width: 1100px;
  margin: 2em auto;
}

a {
  text-decoration: none;
  color: var(--green-1);
}
a:hover, a:focus {
  color: var(--green-3);
}

.btn {
  display: block;
  width: 100%;
  max-width: 30rem;
  margin: 5% auto 0;
  padding: 1rem 3rem;
  border-radius: 0.5rem;
  text-align: center;
  transition: all 0.2s ease-in-out;
}
.btn--violet {
  background: linear-gradient(to right, var(--color__main), var(--violet-2) 45%, var(--color__main));
  color: var(--cyan-0);
  border: 1px solid var(--color__main);
  box-shadow: var(--color__main) 2px 2px 5px;
}
.btn--violet:hover, .btn--violet:focus {
  background: linear-gradient(to right, var(--violet-1), var(--violet-2) 45%, var(--violet-1));
  box-shadow: var(--color__main) 2px 2px 5px, var(--color__main) 0px 0px 3px inset;
  color: var(--cyan-0);
}
.btn--green {
  background: linear-gradient(to right, var(--green-1), var(--green-3) 45%, var(--green-1));
  border: 1px solid var(--green-0);
  box-shadow: var(--green-0) 2px 2px 5px;
  color: var(--green-4);
}
.btn--green:hover, .btn--green:focus {
  background: linear-gradient(to right, var(--green-2), var(--green-3) 45%, var(--green-2));
  box-shadow: var(--green-0) 2px 2px 5px, var(--green-0) 0px 0px 3px inset;
  color: var(--green-4);
}

.top {
  position: fixed;
  /* z-index: -1 */
  bottom: 1rem;
  right: 1rem;
  width: 3rem;
  height: 3rem;
  background-color: var(--violet-2);
  background-image: linear-gradient(315deg, var(--violet-1), var(--color__link--alt) 74%);
  border: none;
  border-radius: 50%;
  cursor: pointer;
  transition: 200ms;
}
.top svg {
  fill: #fff;
  width: 1.5rem;
  height: 1.5rem;
  position: absolute;
  transform: translateX(50%) translateY(50%);
}
.top:after {
  content: "Back to Top";
  position: absolute;
  transform: translateY(200%);
  font-size: 15px;
  transition: 200ms;
  color: var(--violet-3);
  font-weight: bolder;
  text-align: center;
}
.top:hover {
  background-color: var(--violet-3);
  background-image: linear-gradient(315deg, var(--violet-1), var(--violet-3) 74%);
}
.top:hover:after {
  color: var(--violet-1);
}
.top:hover:after svg {
  animation: bounce 2s infinite linear;
}

#svg-sprite {
  display: none;
}

.icon {
  position: relative;
  display: inline-block;
  vertical-align: middle;
  width: 3rem;
  height: 0;
  padding-top: 3rem;
}
.icon.smaller {
  width: 2rem;
  padding-top: 2rem;
}
.icon.small {
  width: 2.5rem;
  padding-top: 2.5rem;
}
.icon.big {
  width: 5rem;
  padding-top: 5rem;
}
.icon.bigger {
  width: 8rem;
  padding-top: 8rem;
}
.icon svg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  fill: currentColor;
}

a.iconText {
  color: var(--color__link--alt);
}
a.iconText span {
  display: inline-block;
}
a.iconText span:nth-child(2) {
  margin-left: 0.5em;
}
@media (hover: hover) and (pointer: fine) {
  a.iconText:hover {
    opacity: 0.7;
  }
}

.l-header {
  position: fixed;
  top: 0;
  z-index: 100;
  width: 100%;
  padding: 0.5em 4%;
  background: var(--color__main);
  background-image: var(--grad__main);
}
.l-header-img {
  max-width: 40vw;
}
@media (min-width: 768px) {
  .l-header-img {
    max-width: 30vw;
  }
}
@media (min-width: 960px) {
  .l-header-img {
    max-width: 20vw;
  }
}
.l-header .iconText {
  font-size: 2.5rem;
}

.l-footer, .l-footer-nav {
  width: 100%;
}

.l-footer {
  align-items: flex-start;
  color: var(--cyan-0);
  padding-top: 2em;
  background: var(--color__main);
  background-image: var(--grad__main);
}
.l-footer article {
  padding: 2em;
  flex: 1;
  min-width: 35rem;
}
.l-footer article:first-of-type {
  border: 0 solid var(--violet-1);
  border-right-width: 1px;
}
.l-footer article:first-of-type ul {
  text-align: center;
  margin-top: 1em;
}
.l-footer article > p {
  padding: 1em 0;
  font-size: var(--fsz__big);
}
.l-footer h3 {
  color: var(--color__link--alt);
}
.l-footer-nav {
  min-height: 5rem;
  padding: 0 5%;
  background: var(--color__main);
  border-top: 1px solid var(--violet-1);
}
.l-footer-nav p, .l-footer-nav ul {
  margin: 0 auto;
}
@media (min-width: 600px) {
  .l-footer-nav p, .l-footer-nav ul {
    margin: 0;
  }
}
.l-footer-nav a {
  color: var(--cyan-0);
}
.l-footer-nav a:hover, .l-footer-nav a:focus {
  color: var(--color__link--alt);
}
.l-footer-nav li {
  display: inline;
  margin-right: 1em;
}
.l-footer-nav li:before {
  content: "|";
  padding-right: 0.5em;
  color: var(--violet-1);
}
.l-footer .social {
  display: inline-block;
}
.l-footer .social a {
  display: block;
  width: 4rem;
  padding-top: 4rem;
  color: var(--color__main);
}
@media (hover: hover) and (pointer: fine) {
  .l-footer .social a:hover {
    color: var(--color__link--alt);
  }
}
.l-footer form {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  margin: 0 auto;
}
.l-footer form label {
  width: 100%;
  padding: 0.5em;
}
.l-footer input[type=email] {
  flex: 1 1 100%;
  height: 100%;
  border: 1px solid var(--violet-2);
  border-radius: 4px;
  background: var(--color__main);
  outline: 0;
  padding: 1em;
  color: var(--color__link--alt);
  font-size: 1em;
  transition: all 0.35s ease-out;
}
.l-footer input[type=email]::placeholder {
  color: var(--violet-2);
  text-transform: capitalize;
}
.l-footer input[type=email]:focus {
  background: var(--violet-1);
}
.l-footer input[type=submit] {
  margin-left: -12.1rem;
  width: 12rem;
  height: 100%;
  padding: 1em;
  border: 0;
  border-left: 1px solid var(--violet-2);
  border-radius: 0 4px 4px 0;
  font-size: 1em;
  background: var(--color__main);
  color: var(--color__link--alt);
  cursor: pointer;
  outline: 0;
}

#toggle {
  border: 1px solid var(--violet-2);
  border-radius: 5px;
  display: none;
  background: var(--violet-1);
  color: var(--color__link--alt);
  cursor: pointer;
  font-size: 1.8em;
}
#toggle:focus, #toggle:hover {
  background: var(--color__main);
}
#toggle:after {
  content: "☰";
}
#toggle span {
  /* Only show if can't load stylesheets */
  position: absolute;
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0;
  border: 0;
  overflow: hidden;
}

.l-header ul {
  position: absolute;
  top: 100%;
  right: 4%;
  display: flex;
  flex-direction: column;
  background: var(--color__main);
  background-image: var(--grad__main);
}

.js .l-header ul {
  display: none;
}
.js #toggle {
  display: block;
}
@media (min-width: 768px) {
  .js .l-header ul {
    display: block;
  }
  .js #toggle {
    display: none;
  }
}

.l-header ul.is-active {
  display: block;
}

.nav-link {
  display: block;
  width: 100%;
  padding: 0.4em 0.8em;
  color: var(--color__link--alt);
  font-size: 1.2em;
  text-align: center;
}
.nav-link:hover, .nav-link:focus, .nav-link:active {
  background: var(--color__main);
  color: var(--violet-3);
  border: 0 solid var(--violet-2);
  border-width: 1px 0;
}

@media (min-width: 768px) {
  #toggle {
    display: none;
  }

  .l-header ul {
    position: relative;
    border: 1px solid var(--color__main);
    box-shadow: 0px 0px 5px var(--color__main) inset;
  }

  .l-header li {
    display: inline-block;
  }

  .nav-link:hover,
.nav-link:focus,
.nav-link:active {
    border-width: 0px;
  }
}
@media (min-width: 600px) {
  .top {
    bottom: 2rem;
    width: 4rem;
    height: 4rem;
  }
  .top svg {
    width: 2rem;
    height: 2rem;
  }
}
@media (min-width: 768px) {
  .top {
    bottom: 5rem;
    width: 6rem;
    height: 6rem;
  }
  .top svg {
    width: 3rem;
    height: 3rem;
  }
}

.l-featured > p {
  font-size: var(--fsz__big);
}
.l-featured h1, .l-featured h1 + p {
  flex-basis: 100%;
  text-align: center;
}
.l-featured__video-wrapper {
  overflow: hidden;
  position: relative;
  flex: 1 1 60%;
}
.l-featured__video-wrapper:after {
  padding-top: 56.25%;
  display: block;
  content: "";
}
.l-featured__video-wrapper iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  border: none;
}
@media (min-width: 960px) {
  .l-featured__video-sum > ul:nth-child(2) > li {
    display: block;
    text-align: left;
  }
}
.l-featured__video-sum > a:not([class]) {
  display: block;
  margin-top: 1em;
  text-align: center;
}
.l-featured__video-sum li {
  display: inline-block;
  padding: 0.5rem 0;
  line-height: 1.4em;
}
.l-featured__video-sum > ul > li:before {
  content: "✔";
  color: green;
  margin-right: 0.5em;
}
.l-featured__video-sum > ul li > ul {
  text-align: center;
}
.l-featured__video-sum + h3 {
  flex-basis: 100%;
}
.l-featured__video-sum .cms-icon {
  width: 2rem;
  height: 2rem;
  margin-right: 1rem;
}
.l-featured__features {
  background: linear-gradient(transparent, var(--color__link--alt));
  border: 2px solid var(--violet-1);
  border-radius: 0.5rem;
}
.l-featured__features li {
  justify-content: unset;
  gap: 1em;
  margin: 0 0.5em 0.5em 0;
  padding: 0.5em 1em;
  font-weight: 700;
  text-align: center;
  text-transform: capitalize;
}
.l-featured__features ~ a.btn {
  margin: 0 auto;
}

.l-templates > p {
  font-size: var(--fsz__big);
}
.l-templates__cards {
  flex: 1 1 100%;
  display: grid;
  grid-template-columns: repeat(auto-fit, 25rem);
  place-content: center;
  gap: 1em;
}
.l-templates__cards article {
  padding: 1em;
  border-radius: 0 0 1.5rem 1.5rem;
  background-image: linear-gradient(transparent, var(--color__link--alt));
  box-shadow: 0px 0px 5px 1px var(--color__main) inset, 1px 1px 3px 1px var(--color__main);
}
.l-templates__cards article img {
  display: block;
  overflow: hidden;
  border: 1px solid var(--violet-2);
  box-shadow: 0px 0px 2px 2px var(--violet-2);
  margin-bottom: 1em;
}
.l-templates__cards article p {
  padding: 1em 0;
  text-align: center;
}

.l-plugins > p {
  font-size: var(--fsz__big);
}
.l-plugins:after {
  content: "";
  width: 100%;
  height: 0;
}
.l-plugins > *:last-child {
  order: 6;
}
.l-plugins a.btn:last-of-type {
  margin-top: 1em;
}
.l-plugins article p {
  display: inline-block;
  vertical-align: middle;
  max-width: 70%;
}
.l-plugins__icon {
  border-radius: 50%;
  width: 6rem;
  padding-top: 6rem;
  margin-right: 1em;
}

.l-testimonials > p {
  font-size: var(--fsz__big);
}
.l-testimonials article {
  max-width: 40rem;
  align-self: stretch;
  margin: 0 auto;
}
.l-testimonials__icon {
  margin: 0 auto 1em;
  width: 25%;
  padding-top: 25%;
}
.l-testimonials__icon svg {
  border: 2px solid var(--color__main);
  border-radius: 100%;
}
.l-testimonials aside ul {
  margin-top: 2em;
  display: flex;
  flex-wrap: wrap;
  gap: 1.5em;
  place-content: space-between center;
}
.l-testimonials .brand-logo {
  display: block;
  padding-top: 3rem;
  width: 12rem;
  fill: black;
}
.l-testimonials section {
  position: relative;
  margin-top: 3em;
  gap: 1em;
  justify-content: center;
}
.l-testimonials figure {
  flex: 1 0 25rem;
  max-width: 35rem;
  padding: 2rem;
  border-radius: 1rem;
  box-shadow: 0px 0px 1px grey, 1px 1px 3px 1px black;
}
.l-testimonials figure:nth-child(even) {
  background: var(--color__main);
  color: var(--color__link--alt);
  box-shadow: 0px 0px 1px 1px var(--color__main), 1px 1px 3px 1px var(--violet-1), 0px 0px 3px var(--color__link--alt) inset;
}
.l-testimonials figure:nth-child(odd) {
  background: var(--color__link--alt);
  color: var(--color__main);
  box-shadow: 0px 0px 1px 1px var(--color__main), 1px 1px 3px 1px var(--violet-1), 0px 0px 3px var(--color__main) inset;
}
.l-testimonials blockquote {
  font-style: italic;
}
.l-testimonials blockquote:before, .l-testimonials blockquote:after {
  content: "“";
  position: absolute;
  font-size: 3em;
  color: var(--color__main);
  margin: -0.4em;
}
.l-testimonials blockquote:after {
  margin: 0 -0.4em -1em 0;
  content: "”";
}
.l-testimonials figure:nth-child(even) blockquote:before,
.l-testimonials figure:nth-child(even) blockquote:after {
  color: var(--color__link--alt);
}
.l-testimonials figcaption {
  margin-top: 1em;
  text-align: right;
}

/* MAIN LAYOUT */
.l-featured, .l-templates, .l-plugins, .l-testimonials {
  gap: 2em;
}

@keyframes bounce {
  0% {
    transform: translateX(50%) translateY(50%);
  }
  25% {
    transform: translateX(50%) translateY(25%);
  }
  50% {
    transform: translateX(50%) translateY(50%);
  }
  75% {
    transform: translateX(50%) translateY(35%);
  }
  100% {
    transform: translateX(50%) translateY(50%);
  }
}